
.track{border-bottom:1px solid #eee;color:#999;cursor:pointer;display:-ms-flexbox;display:flex;padding:10px 10px 10px 0
}
.track:last-child{border-bottom:none
}
.track:hover{color:#DD2476
}
.track.active{color:#FF512F
}
.track .track-state{margin-right:10px;position:relative
}
.track .track-name{pointer-events:none
}
.track .remove-track{width:20px;height:20px;color:#999;cursor:pointer;margin-left:auto;position:relative;left:4px
}
.track .remove-track:hover{color:#ec213a
}
.track.not-streamable .track-name{color:#ec213a;text-decoration:line-through
}
.track.not-streamable .track-state{color:#ec213a
}



