
.playback-controls{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between
}
.playback-controls .playback-control{color:#666;cursor:pointer;margin:0 10px
}
.playback-controls .playback-control.prev{margin-left:0
}
.playback-controls .playback-control.active{color:#DD2476
}
.playback-controls .playback-control.repeat.repeat-on{color:#DD2476
}
.playback-controls .playback-control.repeat.repeat-one{color:#DD2476;position:relative
}
.playback-controls .playback-control.repeat.repeat-one:after{content:'1';font-family:monospace;width:10px;height:10px;background:#DD2476;border-radius:50%;border:1px solid #fff;color:#fff;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;font-size:7px;font-weight:bold;position:absolute;top:0;right:-5px;text-indent:0.5px
}
.playback-controls .track-progress{-webkit-appearance:none;-moz-appearance:none;appearance:none;background:rgba(255,255,255,0);background:linear-gradient(to right, #DD2476, #FF512F);border-radius:9999px;cursor:pointer;margin-left:10px;width:100%;height:15px;overflow:hidden;position:relative
}
.playback-controls .track-progress:after{border-radius:9999px;content:"";height:13px;background:#fff;z-index:1;position:absolute;top:1px;left:1px;right:1px
}
.playback-controls .track-progress::-webkit-progress-bar{border-radius:9999px;background:rgba(255,255,255,0);position:relative;z-index:2
}
.playback-controls .track-progress::-moz-progress-bar{background:linear-gradient(to right, #DD2476, #FF512F)
}
.playback-controls .track-progress::-webkit-progress-value{background:linear-gradient(to right, #DD2476, #FF512F)
}



